@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700;800&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 10px;
    text-decoration: none;
    list-style: none;
    font-family: 'Red Hat Display';
    font-family: 'poppins';
}

.App{
    max-width: 1440px;
    margin: 0 auto;
}
.day {
    width: 85vw;
    max-width: 400px;
    min-width: 250px;
    background-color: #F4C430;
    border-radius: 12px;
    padding: 5rem 1rem;
    margin: 2rem auto;
}

.date_and_city_d h1 {
    font-size: 3rem;
    text-align: center;
}

.date_and_city_d p {
    font-size: 1.6rem;
    text-align: center;
}

.temperature_d p {
    font-size: 5rem;
    text-align: center;
    margin: 1rem 0;
}

.humidity_d {
    display: flex;
    align-items: center;
    justify-content: center;
}

.humidity_d img {
    width: 64px;
    margin-right: 1rem;
}

.humidity_d p {
    font-size: 2.5rem;
}
.popup_wrapper{
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border-radius: 12px;
    border: 2px solid #157FFB;
    background-color: #fff;
    padding: 2rem;
}

.popup {
    display: none;
}

.active_popup {
    position: absolute;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: #fff;
}

.cross {
    position: absolute;
    right: 30px;
    top: 20px;
    width: 15px;
    height: 15px;
    opacity: 0.3;
    cursor: pointer;
}

.cross:hover {
    opacity: 1;
}

.cross:before,
.cross:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 2px;
    background-color: #157FFB;
}

.cross:before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.cross:after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.select_city_form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 7rem;
}

.select_city_form input {
    width: 80vw;
    max-width: 500px;
    font-size: 1.6rem;
    border: 2px solid #A9B6CC;
    padding: 1rem;
    border-radius: 12px;
    outline: none;
}

.select_city_form button {
    width: 80vw;
    max-width: 500px;
    font-size: 1.6rem;
    color: #fff;
    background-color: #157FFB;
    border: none;
    padding: 1rem;
    margin-top: 1rem;
    border-radius: 12px;
    cursor: pointer;
}
.next_four_day {
    width: 85vw;
    max-width: 400px;
    min-width: 250px;
    background-color: #A9B6CC;
    border-radius: 12px;
    padding: 5rem 1rem;
    margin: 2rem auto;
}

.date_and_city p {
    font-weight: 700;
    font-size: 1.6rem;
    text-align: center;
}

.mean_temperature {
    font-size: 5rem;
    text-align: center;
}

.temperature p:nth-child(2) {
    font-size: 2rem;
    text-align: center;
}

.morning_temperature,
.day_temperature,
.night_temperature,
.humidity {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1.5rem 0;
}

.morning_temperature img,
.day_temperature img,
.night_temperature img,
.humidity img {
    width: 64px;
    margin-right: 4rem;
}

.morning_temperature p,
.day_temperature p,
.night_temperature p,
.humidity p {
    font-size: 2rem;
}

.temperature {
    margin: 2rem 0;
}

.next_four_days_wrapper{
    display: flex;
    flex-wrap: wrap;
}
